<template>
  <div class="score-talbe">
    <div class="score-table-row">
      <div
      class="score-table-item"
      v-for="item in tableData1"
        :key="item.title"
      >
        <div class="st-item-title">{{ item.title }}</div>
        <dv-charts class="ring-charts" :option="item.ring" v-if="!loading" />
        <div class="st-item-content" v-if="!loading" >
          <div class="number">{{ item.score }}</div>
          <div class="unit">分</div>
        </div>
        <!-- <dv-decoration-9 v-show="loading" class="is-loading">加载中...</dv-decoration-9> -->
        <dv-loading v-if="loading">{{ loadingText }}</dv-loading>
      </div>
    </div>
    <!-- <div class="row-decoration">
      <dv-decoration-10 />
    </div> -->
    <div class="score-table-row">
      <div
      class="score-table-item"
      v-for="item in tableData2"
        :key="item.title"
      >
        <div class="st-item-title">{{ item.title }}</div>
        <dv-charts class="ring-charts" :option="item.ring" v-if="!loading" />
        <div class="st-item-content" v-if="!loading">
          <div class="number">{{ item.score }}</div>
          <div class="unit">分</div>
        </div>
        <!-- <dv-decoration-9 v-show="loading" class="is-loading">加载中...</dv-decoration-9> -->
        <dv-loading v-if="loading">{{ loadingText }}</dv-loading>
      </div>
      <div class="score-total-item">
        <div class="st-total-title">总分</div>
        <div class="st-total-content">
          <div class="total-score">{{ totalScore }}</div>
          <div class="total-unit">分</div>
        </div>
        <!-- <dv-active-ring-chart :config="config" class="score-total-ring" /> -->
      </div>
    </div>
  </div>
</template>

<script>
import { getTable } from '@/api'
import { setInterval } from 'timers'
export default {
  name: 'ScoreTable',
  props: {
    rowNumber: {
      type: Number,
      default: 6
    }
  },
  data () {
    return {
      loading: true,
      loadingText: '加载中...',
      tableData: [],
      tableData1: [],
      tableData2: [],
      config: {
        data: []
      },
      totalScore: 0,
      initData: [
        {
          title: '财务入账及时性',
          score: 0,
          percent: 0
        },
        {
          title: '是否管理责任到人',
          score: 0,
          percent: 0
        },
        {
          title: '执行率',
          score: 0,
          percent: 0
        },
        {
          title: '费用节约率',
          score: 0,
          percent: 0
        },
        {
          title: '办公用房整体超标情况',
          score: 0,
          percent: 0
        },
        {
          title: '单车运行平均费用率',
          score: 0,
          percent: 0
        },
        {
          title: '总费用超定额率',
          score: 0,
          percent: 0
        },
        {
          title: '办公设备共享率',
          score: 0,
          percent: 0
        },
        {
          title: '使用年限执行率',
          score: 0,
          percent: 0
        },
        {
          title: '办公设备国产化率',
          score: 0,
          percent: 0
        },
        {
          title: '办公软件国产率化',
          score: 0,
          percent: 0
        }
      ],
      localData: [
        {
          title: '财务入账及时性',
          score: 3,
          percent: 50
        },
        {
          title: '是否管理责任到人',
          score: 4,
          percent: 60
        },
        {
          title: '执行率',
          score: 1,
          percent: 163
        },
        {
          title: '费用节约率',
          score: 2,
          percent: 65
        },
        {
          title: '办公用房整体超标情况',
          score: 5,
          percent: 23
        },
        {
          title: '单车运行平均费用率',
          score: 4,
          percent: 56
        },
        {
          title: '总费用超定额率',
          score: 1282,
          percent: 20
        },
        {
          title: '办公设备共享率',
          score: 36,
          percent: 15
        },
        {
          title: '使用年限执行率',
          score: 0,
          percent: 0
        },
        {
          title: '办公设备国产化率',
          score: 1,
          percent: 35
        },
        {
          title: '办公软件国产率化',
          score: 6,
          percent: 46
        }
      ]
    }
  },
  created () {
    this.formatTableData(this.initData)
    this.getTableAsync()

    setInterval(() => {
      this.refreshRing()
    }, 8000)
  },
  methods: {
    async getTableAsync () {
      const { status, data } = await getTable()
      if (status === 200) {
        this.loading = false
        this.tableData = data
        this.formatTableData(data)
      } else {
        this.loadingText = '加载失败，将启用本地数据'
        setTimeout(() => {
          this.loading = false
          this.tableData = this.localData
          this.formatTableData(this.localData)
        }, 3000)
      }
    },
    formatTableData (data) {
      this.tableData1 = []
      this.tableData2 = []
      this.totalScore = 0
      data.forEach((item, index) => {
        item.ring = {
          series: [
            {
              type: 'gauge',
              startAngle: -Math.PI / 2,
              endAngle: Math.PI * 1.5,
              arcLineWidth: 13,
              radius: '80%',
              data: [
                { name: '百分比', value: item.percent }
              ],
              axisLabel: {
                show: false
              },
              axisTick: {
                show: false
              },
              pointer: {
                show: false
              },
              backgroundArc: {
                style: {
                  stroke: '#224590'
                }
              },
              details: {
                show: true,
                formatter: '{value}%',
                style: {
                  fill: '#1ed3e5',
                  fontSize: 20
                }
              }
            }
          ],
          color: ['#03d3ec']
        }

        if (index < this.rowNumber) {
          this.tableData1.push(item)
        } else {
          this.tableData2.push(item)
        }
        let conf = {}
        conf.name = item.title
        conf.value = item.percent
        this.config.data.push(conf)
        this.totalScore += item.score
      })
    },
    randomExtend (minNum, maxNum) {
      if (arguments.length === 1) {
        return parseInt(Math.random() * minNum + 1, 10)
      } else {
        return parseInt(Math.random() * (maxNum - minNum + 1) + minNum, 10)
      }
    },
    refreshRing () {
      let refresh = []
      this.tableData.forEach(item => {
        let temp = { ...item }
        temp.percent = 0
        refresh.push(temp)
        temp = null
      })
      this.formatTableData(refresh)
      setTimeout(() => {
        this.formatTableData(this.tableData)
      }, 500)
    }
  }
}
</script>

<style lang="less">
.score-talbe {
  width: 100%;
  height: 100%;
  // padding: 20px 15px 15px 20px;
  box-sizing: border-box;

  .score-table-row {
    display: flex;
    justify-content: flex-start;
    height: 49%;
    margin-top: 15px;

    &:first-child {
      margin-top: 0;
    }

    .score-table-item {
      width: 16.66%;
      height: 100%;
      margin-left: 15px;
      box-sizing: border-box;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      border-top: 2px solid #05729E;
      // border-right: 2px solid #05729E;
      background-color: rgba(6, 30, 93, 0.5);
      position: relative;

      &:first-child {
        margin-left: 0;
      }

      .is-loading {
        margin-top: 20%;
        width: 100px;
        height: 100px;
        font-size: 12px;
      }

      .st-item-title {
        width: 100%;
        font-size: 20px;
        box-sizing: border-box;
        padding: 10px 0 10px 10px;
      }

      .dv-charts-container {
        width: 60%;
        height: 60%;
      }

      .st-item-content {
        position: absolute;
        right: 5px;
        bottom: 5px;
        display: flex;
        justify-content: flex-end;
        padding: 5px 10px 0px 10px;
        box-sizing: border-box;
        width: 50%;
        background-color: rgba(6, 30, 93, 0.7);
        border-radius: 3px;

        .number {
          font-size: 40px;
          color: #F36728;
          text-align: end;
          padding: 0 0 0 20px;
          // width: 90px;
        }

        .unit {
          font-size: 16px;
          margin-left: 10px;
          display: flex;
          align-items: flex-end;
          margin-bottom: 6px;
        }
      }
    }

    .score-total-item {
      width: 16.66%;
      height: 100%;
      margin-left: 15px;
      box-sizing: border-box;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      border-top: 2px solid #05729E;
      // border-right: 2px solid #05729E;
      background-color: rgba(6, 30, 93, 0.5);

      .score-total-ring {
        width: 100%;
        height: 100%;

        .active-ring-name {
          width: 90%;
          bottom: 5%;
          position: absolute;
          font-size: 18px !important;
        }
      }

      .st-total-title {
        font-size: 30px;
        box-sizing: border-box;
        padding-bottom: 20px;
      }

      .st-total-content {
        width: 100%;
        display: flex;
        justify-content: center;

        .total-score {
          font-size: 50px;
          color: #F36728;
          text-align: end;
          width: 50%;
        }

        .total-unit {
          font-size: 16px;
          margin-left: 15px;
          display: flex;
          align-items: flex-end;
          margin-bottom: 8px;
        }
      }
    }
  }

  .row-decoration {
    width: 100%;
    position: relative;
    .dv-decoration-10 {
      position: relative;
      width: 100%;
      height: 9px;
      top: 1px;
      // bottom: 0;
    }
  }

}
</style>



