<template>
  <div id="data-view">
    <dv-full-screen-container>
      <div class="main-header">
        <div class="mh-middle">Soul Racer 主管单位资产合理使用绩效监控大屏</div>
        <div class="mh-right">
          <show-time></show-time>
          <!-- <dv-border-box-2
            style="width: 120px; height: 50px; line-height: 50px; text-align:center;margin-left:200px;"
          >
            数据导入
          </dv-border-box-2> -->
        </div>
      </div>

      <dv-border-box-1 class="main-container">
        <div class="main-container-body">
          <div class="body-left">
            <div class="score-table-container">
              <score-table></score-table>
            </div>
            <div class="detail-rank-container">
              <details-rank-one></details-rank-one>
              <!-- <div class="rank-decoration"></div> -->
              <details-rank-two></details-rank-two>
              <!-- <div class="rank-decoration"></div> -->
              <details-rank-three></details-rank-three>
              <!-- <div class="rank-decoration"></div> -->
              <details-rank-four></details-rank-four>
            </div>
          </div>

          <div class="body-right">
            <div class="rank-content">
              <dv-border-box-3 class="total-rank-container">
                <total-rank></total-rank>
              </dv-border-box-3>
            </div>
            <!-- <dv-border-box-8 class="time-container">
              <img src="./img/logo.png" alt="">
            </dv-border-box-8> -->
          </div>
        </div>
      </dv-border-box-1>

    </dv-full-screen-container>
  </div>
</template>

<script>
import ScoreTable from './score-table'
import TotalRank from './total-rank'
import DetailsRankOne from './details-rank-one'
import DetailsRankTwo from './details-rank-two'
import DetailsRankThree from './details-rank-three'
import DetailsRankFour from './details-rank-four'
import ShowTime from './show-time'

export default {
  name: 'DataView',
  components: {
    ScoreTable,
    TotalRank,
    DetailsRankOne,
    DetailsRankTwo,
    DetailsRankThree,
    DetailsRankFour,
    ShowTime
  },
  data () {
    return {}
  }
}
</script>

<style lang="less">
#data-view {
  width: 100%;
  height: 100%;
  background-color: #030409;
  color: #fff;

  #dv-full-screen-container {
    background-image: url('./img/bg.png');
    background-size: 100% 100%;
    box-shadow: 0 0 3px blue;
    display: flex;
    flex-direction: column;
  }

  .main-header {
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    position: relative;

    .mh-left {
      font-size: 20px;
      color: rgb(1,134,187);

      a:visited {
        color: rgb(1,134,187);
      }
    }

    .mh-middle {
      font-size: 36px;
      line-height: 50px;
    }

    .mh-left, .mh-right {
      width: 450px;
      position: absolute;
      right: 0;
      top: 25px;
    }
  }

  .main-container {
    height: calc(~"100% - 80px");

    .main-container-body {
      height: 100%;
      padding: 1.5% 1.5%;
      display: flex;

      .body-left {
        width: 75%;
        .score-table-container {
          height: 50%;
        }

        .detail-rank-container {
          margin-top: 2%;
          // margin-left: 1%;
          height: 41%;
          display: flex;

          .border-box-content {
            display: flex;

            .rank-decoration {
              // margin-top: 30px;
              height: 90%;
              width: 3px;
              background-color: #7F0209;
            }
          }
        }
      }

      .body-right {
        margin-left: 1%;
        height: 100%;
        flex: 1;
        .time-container {
          height: 20%;

          .border-box-content {
            display: flex;
            justify-content: center;
            align-items: center;
          }
        }
        .rank-content {
          margin-bottom: 3%;
          display: flex;
          height: 94%;
          .total-rank-container {
            height: 100%;
          }
        }
      }
    }
  }
}
</style>



