<template>
  <dv-border-box-10 class="details-rank-two">
    <div class="table-name">
      <img src="./img/打印机.png" />人均占有打印机单位排行
    </div>

    <div class="rank-box" v-if="!loading">
      <dv-scroll-board :config="config" />
    </div>
    <dv-loading v-else>{{ loadingText }}</dv-loading>
  </dv-border-box-10>
</template>

<script>
import { getRankValue } from '@/api'
export default {
  name: 'DetailsRankTwo',
  data () {
    return {
      loading: true,
      loadingText: '加载中...',
      localData: [
        { name: '阿里巴巴', 'score': 6 },
        { name: '百度', 'score': 5 },
        { name: '京东', 'score': 4 },
        { name: '腾讯', 'score': 3 },
        { name: '联想', 'score': 2 },
        { name: '久其软件', 'score': 1 }
      ],
      config: {
        data: [],
        index: true,
        columnWidth: [50],
        headerHeight: 50,
        align: ['center'],
        oddRowBGC: 'rgba(9, 37, 50, 0.5)',
        evenRowBGC: 'rgba(35, 69, 143, 0.3)'
      }
    }
  },
  created () {
    this.getRankValueAsync()
  },
  methods: {
    async getRankValueAsync () {
      const { status, data } = await getRankValue()
      if (status === 200) {
        this.loading = false
        data.forEach(item => {
          let rank = []
          rank.push(item.name)
          rank.push(item.score + '分')
          this.config.data.push(rank)
        })
        this.config = JSON.parse(JSON.stringify(this.config))
      } else {
        this.loadingText = '加载失败，将启用本地数据'
        setTimeout(() => {
          this.localData.forEach(item => {
            this.loading = false
            let rank = []
            rank.push(item.name)
            rank.push(item.score + '分')
            this.config.data.push(rank)
          })
          this.config = JSON.parse(JSON.stringify(this.config))
        }, 3000)
      }
    }
  }
}
</script>

<style lang="less">
.details-rank-two {
  width: calc(~"25% + 10px");
  height: 100%;
  margin: 0 -5px;
  box-sizing: border-box;
  margin-right: 2%;

  &.dv-border-box-10 {
    width: calc(~"25% + 10px");
  }

  .border-box-content {
    flex-direction: column;
    padding: 20px;
    box-sizing: border-box;
  }

  .is-loading {
    width: 150px;
    height: 150px;
    margin-left: 25%;
    margin-top: 20%;
  }

  .table-name {
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 15px;
    font-size: 18px;

    img {
      width: 40px;
      height: 40px;
      margin-right: 5px;
    }
  }

  .rank-box {
    height: calc(~"100% - 60px");
  }

  .dv-scroll-board {
    height: 100%;

    .row-item {
      .index {
        border-radius: 5px;
        padding: 3px 7px;
        font-size: 16px;
      }

      .ceil {
        font-size: 16px;

        &:nth-child(2) {
          flex: 1;
        }

        &:last-child {
          text-align: right;
          color: #18ABF3;
          width: 20% !important;
        }
      }
    }
  }
}
</style>



