<template>
  <div class="total-rank">
    <div class="total-rank-header">
      <dv-decoration-1 style="width:100px;height:50px;transform:rotateY(180deg)" />
      <div>总分排名情况</div>
      <dv-decoration-1 style="width:100px;height:50px;" />
    </div>
    <div id="ranking-board">
      <dv-scroll-ranking-board :config="config" unit="分" v-if="!loading" />
      <!-- <dv-decoration-9 v-show="loading" class="is-loading">加载中...</dv-decoration-9> -->
      <dv-loading v-else>{{ loadingText }}</dv-loading>
    </div>
  </div>
</template>

<script>
import { getAllRank } from '@/api'
import { setInterval, setTimeout } from 'timers'
export default {
  name: 'TotalRank',
  data () {
    return {
      loading: true,
      loadingText: '加载中..',
      localData: [
        {
          name: '百度',
          value: 55
        },
        {
          name: '阿里巴巴',
          value: 120
        },
        {
          name: '腾讯',
          value: 78
        },
        {
          name: '中国联通',
          value: 66
        },
        {
          name: '中国移动',
          value: 88
        },
        {
          name: '字节跳动',
          value: 65
        },
        {
          name: '久其软件',
          value: 110
        },
        {
          name: '小米',
          value: 70
        },
        {
          name: '南方电网',
          value: 20
        },
        {
          name: '华为',
          value: 10
        }
      ],
      config: {
        data: [],
        rowNum: 10
      }
    }
  },
  created () {
    this.getAllRankAsync()
  },
  methods: {
    async getAllRankAsync () {
      const { status, data } = await getAllRank()
      if (status === 200) {
        this.loading = false
        this.config.data = data
        this.config = JSON.parse(JSON.stringify(this.config))
        this.setRankAnimation()
      } else {
        this.loadingText = '加载失败，将启用本地数据'
        setTimeout(() => {
          this.loading = false
          this.config.data = this.localData
          this.config = JSON.parse(JSON.stringify(this.config))
          this.setRankAnimation()
        }, 3000)
      }
    },
    setRankAnimation () {
      setInterval(() => {
        let rows = document.querySelectorAll('#ranking-board .row-item')
        rows.forEach((dom, index) => {
          dom.className = `row-item animate_${index + 1}`

          setTimeout(() => {
            dom.className = `row-item`
          }, parseInt((index + 1) * 300))
        })
      }, 10000)
    }
  }
}
</script>

<style lang="less">
.total-rank {
  padding: 4% 5%;
  height: 100%;
  box-sizing: border-box;
  .total-rank-header {
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 26px;
  }

  .is-loading {
    width: 150px;
    height: 150px;
    margin-left: 25%;
    margin-top: 60%;
  }

  #ranking-board {
    width: 100%;
    height: calc(100% - 80px);
    box-shadow: 0 0 3px blue;
    background-color: rgba(6, 30, 93, 0.5);
    border-top: 2px solid rgba(1, 153, 209, .5);
    box-sizing: border-box;
    padding: 0px 30px;

    .dv-scroll-ranking-board {
      height: 100%;
      padding: 30px 0;
      box-sizing: border-box;
      display: flex;
      flex-direction: column;

      .inside-column {
        opacity: 0.8;
      }

      .row-item {
        flex: 1;

        .ranking-info {
          font-size: 16px;
          line-height: 16px;
          align-items: center;

          .rank {
            width: 50px;
          }
        }

        .ranking-column {
          .inside-column {
            height: 20px;

            .shine {
              top: -2px;
              height: 5px;
              width: 50px;
              background: radial-gradient(#06D2EB 10%, transparent 80%);
              animation: shine 5s ease-in-out infinite
            }
          }
        }
      }
    }
  }
}

.animate_1 {
  animation: rotateRank 0.3s linear 0s alternate;
}
.animate_2 {
  animation: rotateRank 0.3s linear 0.3s alternate;
}
.animate_3 {
  animation: rotateRank 0.3s linear 0.6s alternate;
}
.animate_4 {
  animation: rotateRank 0.3s linear 0.9s alternate;
}
.animate_5 {
  animation: rotateRank 0.3s linear 1.2s alternate;
}
.animate_6 {
  animation: rotateRank 0.3s linear 1.5s alternate;
}
.animate_7 {
  animation: rotateRank 0.3s linear 1.8s alternate;
}
.animate_8 {
  animation: rotateRank 0.3s linear 2.1s alternate;
}
.animate_9 {
  animation: rotateRank 0.3s linear 2.4s alternate;
}
.animate_10 {
  animation: rotateRank 0.3s linear 2.7s alternate;
}

@keyframes rotateRank {
  0% {
    transform: rotateX(0deg)
  }
  50% {
    transform: rotateX(180deg)
  }
  100% {
    transform: rotateX(0deg)
  }
}
</style>



