<template>
  <div class="date-container">
    <div class="date">{{ date }}</div>
    <div class="day">{{ day }}</div>
    <div class="time">{{ time }}</div>
  </div>
</template>

<script>
import moment from 'moment'
moment.locale('zh-cn')
export default {
  name: 'ShowTime',
  data () {
    return {
      date: '',
      day: '',
      time: ''
    }
  },
  methods: {
    getTime () {
      this.date = moment(new Date()).format('YYYY年MM月DD日')
      this.day = moment(new Date()).format('dddd')
      this.time = moment(new Date()).format('HH:mm:ss')
    }
  },
  created () {
    this.getTime()
    setInterval(() => {
      this.getTime()
    }, 1000)
  }
}
</script>

<style lang="less">
.date-container {
  display: flex;
  justify-content: flex-end;
  padding-right: 10%;
  align-items: center;
  font-size: 24px;
  color: #06D2EB;

  .date {
    margin-right: 10px;
  }
  .day {
    margin-right: 10px;
  }
}
</style>



